import React from "react";
import { useEffect } from "react";
import { useState } from "react";
import PieChart from "../components/PieChart";
import {
  getFechasXClient,
  getFechasXProyecto,
  getProyectos,
  getReporteCliente,
  getUsers,
} from "../service/apiDashboardService";
import "react-date-range/dist/styles.css";
import "react-date-range/dist/theme/default.css";
import { startOfMonth, endOfMonth, sub } from "date-fns";
import DropDowns from "../components/DropDowns";
import Totales from "../components/Totales";
import TableTickets from "../components/TableTickets";
import BarChart from "../components/BarChart";
import { AuthProvider } from "../auth/context/AuthProvider";

const Dashboards = () => {
  // const [clientSelectedId, setClientSelectedId] = useState("Cliente");
  const [fechasXCliente, setFechasXCliente] = useState([]);
  const [fechasXProyecto, setFechasXProyecto] = useState([]);
  const [projects, setProjects] = useState([]);
  const [users, setUsers] = useState([]);
  const [reporteCliente, setReporteCliente] = useState([]);
  const [clientSelected, setClientSelected] = useState("Cliente");
  const [clientSelectedId, setClientSelectedId] = useState("Cliente");
  const [projectSelected, setProjectSelected] = useState("Proyecto");
  const [totalHoras, setTotalHoras] = useState();
  const [proyectosTotales, setProyectosTotales] = useState();

  const today = new Date();
  const monthPrev = sub(today, { months: 1 });
  const [datePicker, setDatePicker] = useState([
    {
      startDate: startOfMonth(monthPrev),
      endDate: endOfMonth(monthPrev),
      key: "selection",
    },
  ]);
  const [dateFrom, setDateFrom] = useState(
    datePicker[0].startDate.toISOString().split("T", 1)
  );
  const [dateTo, setDateTo] = useState(
    datePicker[0].endDate.toISOString().split("T", 1)
  );
  const [seriesPie, setSeriesPie] = useState([]);
  const [pieChart, setPieChart] = useState({
    chart: {
      id: "pie-chart",
    },
    dataLabels: {
      enabled: true,
    },
    plotOptions: {
      pie: {
        donut: {
          size: "80px",
        },
      },
    },
    legend: {
      show: true,
      position: "bottom",
      width: "50px",
      fontFamily: "'Montserrat', sans-serif",
      labels: {
        colors: "#8898aa",
      },
    },
    colors: ["#ff931e", "#f1f1f1", "#f17d30", "#909295", "#0000ff"],
    tooltip: {
      fillSeriesColor: false,
      theme: "dark",
    },
    labels: [],
  });
  const [seriesBar, setSeriesBar] = useState([]);
  const [barChart, setBarChart] = useState({
    colors: ["#ff931e"],
    dataLabels: {
      enabled: false,
    },
    xaxis: {
      labels: {
        style: {
          cssClass: "grey--text lighten-2--text fill-color",
        },
      },
    },
    plotOptions: {
      bar: {
        horizontal: false,
      },
    },
    grid: {
      borderColor: "rgba(0,0,0,0.1)",
    },
    yaxis: {
      labels: {
        style: {
          cssClass: "grey--text lighten-2--text fill-color",
        },
      },
    },
    tooltip: {
      theme: "dark",
    },
  });

  const onChangeDate = (item) => (
    setDatePicker([item.selection]),
    setDateFrom([item.selection][0].startDate.toISOString().split("T", 1)),
    setDateTo([item.selection][0].endDate.toISOString().split("T", 1))
  );

  const client = reporteCliente.filter((r) => r.client === clientSelected);
  const proyectos = projects.filter((r) => r.client === clientSelected);
  const unProyecto = projects.filter((p) => p.name === projectSelected);
  const clientProjects = projects.filter((r) => r.client === clientSelected);
  const tickets = fechasXCliente.filter((t) => t.ticketId > 0);

  const getFechasXClientApi = async () => {
    try {
      const fechasxclient = await getFechasXClient.fechas(
        clientSelectedId,
        dateFrom,
        dateTo
      );
      setFechasXCliente(fechasxclient.data);
    } catch (error) {
      console.log(error);
    }
  };
  // TODO VER QUE LE PASA A LOS PROYECTOS
  const getFechasXProyectoApi = async () => {
    try {
      const projectId = unProyecto[0].id;
      // const projectId = unProyecto.id;
      const fechasxproyecto = await getFechasXProyecto.fechas(
        projectId,
        dateFrom,
        dateTo
      );
      setFechasXProyecto(fechasxproyecto.data);
    } catch (error) {
      console.log(error);
    }
  };
  const getReporteClienteApi = async () => {
    try {
      const clientes = await getReporteCliente.clientes;
      setReporteCliente(clientes.data);
    } catch (error) {
      console.log("cliente error", error);
    }
  };
  const getUsersApi = async () => {
    try {
      const usuarios = await getUsers.users;
      setUsers(usuarios.data);
    } catch (error) {
      console.log(error);
    }
  };
  const getProyectosApi = async () => {
    try {
      const proyectos = await getProyectos.proyectos;
      setProjects(proyectos.data);
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    getReporteClienteApi();
    getProyectosApi();
    getUsersApi();
  }, []);

  useEffect(() => {
    getFechasXClientApi();
    // getFechasXProyectoApi();
  }, [dateFrom, dateTo, clientSelectedId, projectSelected]);

  useEffect(() => {
    setTotalHoras(
      clientSelected === "Cliente"
        ? reporteCliente
            .map((r) => {
              return r.horas;
            })
            .reduce((prev, current) => prev + Math.round(current), 0)
        : client
            .map((c) => {
              return c.horas;
            })
            .reduce((prev, current) => prev + Math.round(current), 0)
    );
    setProyectosTotales(
      clientSelected === "Cliente"
        ? reporteCliente
            .map((r) => {
              return r.projects;
            })
            .reduce((prev, current) => prev + current, 0)
        : client.map((c) => {
            return c.projects;
          })
    );
    // debugger
    setSeriesPie(
      clientSelected === "Cliente" && projectSelected === "Proyecto"
        ? reporteCliente.map((r) => {
            return r.horas;
          })
        : projectSelected !== "Proyecto"
        ? unProyecto.map((c) => {
            return c.horas;
          })
        : client.map((c) => {
            return c.horas;
          })
    );
    setPieChart(
      clientSelected === "Cliente" && projectSelected === "Proyecto"
        ? {
            labels: reporteCliente.map((r) => {
              return r.client;
            }),
          }
        : projectSelected !== "Proyecto"
        ? {
            labels: unProyecto.map((r) => {
              return r.client;
            }),
          }
        : {
            labels: client.map((r) => {
              return r.client;
            }),
          }
    );
    setSeriesBar([
      clientSelected === "Cliente" && projectSelected === "Proyecto"
        ? {
            data: projects.map((r) => {
              return r.horas;
            }),
            name: "Horas",
          }
        : projectSelected !== "Proyecto"
        ? {
            data: unProyecto.map((r) => {
              return r.horas;
            }),
            name: "Horas",
          }
        : {
            data: proyectos.map((r) => {
              return r.horas;
            }),
            name: "Horas",
          },
    ]);
    setBarChart(
      clientSelected === "Cliente" && projectSelected === "Proyecto"
        ? // traeme todos
          {
            xaxis: {
              categories: projects.map((r) => {
                return r.name;
              }),
            },
          }
        : // traeme los del cliente
        projectSelected !== "Proyecto"
        ? {
            xaxis: {
              categories: unProyecto.map((r) => {
                return r.name;
              }),
            },
          }
        : {
            xaxis: {
              categories: proyectos.map((r) => {
                return r.name;
              }),
            },
          }
    );
  }, [reporteCliente, projectSelected, clientSelectedId]);
  return (
    <>
      <div className="dashboard-container container">
        <h1 className="my-4" style={{ color: "#0000ff" }}>
          Reporte de horas
        </h1>
        <div className="col-12 row justify-content-center">
          <div className="col-12 col-sm-6 col-md-4 mb-2">
            <PieChart
              title="Horas por cliente"
              data={seriesPie}
              options={pieChart}
            />
          </div>
          <div className="col-12 col-sm-6 col-md-8">
            <Totales horas={totalHoras} proyectos={proyectosTotales} />
            <DropDowns
              reporteCliente={reporteCliente}
              clientSelected={clientSelected}
              clientSelectedId={clientSelectedId}
              setClientSelected={setClientSelected}
              setClientSelectedId={setClientSelectedId}
              datePicker={datePicker}
              onChange={onChangeDate}
              projects={clientProjects}
              projectSelected={projectSelected}
              setProjectSelected={setProjectSelected}
            />
            <BarChart
              title="Horas por proyecto"
              data={seriesBar}
              options={barChart}
            />
          </div>
          <TableTickets data={tickets} />
        </div>
      </div>
    </>
  );
};

export default Dashboards;
