import React from "react";
import { Card } from "@mui/material";
import HorasTotales from "./HorasTotales"
import ProyectosTotales from "./ProyectosTotales"

const Totales = ({horas, proyectos}) => {
  return (
    <Card className="d-flex row">
      <HorasTotales horas={horas}/>
      <ProyectosTotales proyectos={proyectos} />
    </Card>
  );
};

export default Totales;
