import React from "react";
import { FormControl, InputLabel, Select } from "@mui/material";

const Clientes = ({
  reporteCliente,
  setClientSelected,
  setClientSelectedId,
  setProjectSelected,
}) => {
  const handleChange = (e) => {
    var index = e.nativeEvent.target.selectedIndex;
    setClientSelectedId(e.target.value);
    setClientSelected(e.nativeEvent.target[index].text);
    if (index === 0) {
      setProjectSelected("Proyecto")
    } 
  };

  return (
    <div className="p-3">
      <FormControl>
        <InputLabel htmlFor="age-native-simple">Cliente</InputLabel>
        <Select native onChange={handleChange}>
          <option value="Cliente">Cliente</option>
          {reporteCliente.map((r) => {
            return (
              <option value={r.clientId} key={r.clientId}>
                {r.client}
              </option>
            );
          })}
        </Select>
      </FormControl>
    </div>
  );
};

export default Clientes;
