import axios from "axios";
import environment from "../environment/environment";

const baseUrl = environment.apiBaseUrl;

export const getReporteCliente = {
  clientes: axios.get(`${baseUrl}clients`),
};

export const getUsers = {
  users: axios.get(`${baseUrl}users`),
};

export const getProyectos = {
  proyectos: axios.get(`${baseUrl}projects`),
};

export const getFechasXClient = {
  fechas: (clientId, dateFrom, dateTo) =>
    axios.get(`${baseUrl}fechas/${clientId}/${dateFrom}/${dateTo}`),
};

export const getFechasXProyecto = {
  fechas: (projectId, dateFrom, dateTo) =>
    axios.get(`${baseUrl}fechas/${projectId}/${dateFrom}/${dateTo}`),
};
