import React, { useContext } from "react";
import { Link, useNavigate } from "react-router-dom";
import { AuthContext } from "../auth";
import logodarktext from "../assets/logo-text.png";

const NavBar = () => {
  const { user, logout } = useContext(AuthContext);
  
  const navigate = useNavigate();

  const onLogout = () => {
    logout();
    navigate("/login", {
      replace: true,
    });
  };

  return (
    <div className="container-fluid" style={{ background: "#f8f8f8" }}>
      <div className="p-3 d-flex row justify-content-between">
        <span className="col-2">
          <nav>
            <Link to="/dashboard">
              <img src={logodarktext} alt="homepage" />
            </Link>
          </nav>
        </span>
        <span className="col-7 d-flex row justify-content-start align-items-center">
          <span
            className="col-3 letras-naranja-claro"
            style={{ color: "#ff931e", fontWeight: 600, fontSize: "1.2rem" }}
          >
            <Link
              style={{ color: "#ff931e", textDecoration: "none" }}
              to="/dashboard"
            >
              Dashboard
            </Link>
          </span>
          <span
            className="col-3"
            style={{ color: "#ff931e", fontWeight: 600, fontSize: "1.2rem" }}
          >
            Reportes
          </span>
        </span>
        {/* <span className="">{user?.name}</span> */}
        <span className="col-md-3 d-flex row justify-content-between align-items-center">
        {user ? <span className="col-3" style={{ color: "#ff931e", fontWeight: 600, fontSize: "1.2rem" }}>{user.name}</span> : <span className="col-3"></span>}
          <span
            className="col-3"
            style={{ color: "#ff931e", fontWeight: 600, fontSize: "1.2rem" }}
          >
            Configuracion
          </span>
          <span
            className="col-3"
            style={{ color: "#ff931e", fontWeight: 600, fontSize: "1.2rem" }}
            onClick={onLogout}
          >
            Salir
          </span>
        </span>
      </div>
    </div>
  );
};

export default NavBar;
