import { Button, Modal } from "@material-ui/core";
import { Card } from "@mui/material";
import React from "react";
import Calendar from "./Calendar";
import Clientes from "./Clientes";
import Proyecto from "./Proyecto";
import Usuarios from "./Usuarios";
import SweetAlert2 from "react-sweetalert2";

const DropDowns = ({
  reporteCliente,
  clientSelected,
  clientSelectedId,
  setClientSelected,
  setClientSelectedId,
  datePicker,
  onChange,
  projectSelected,
  setProjectSelected,
  projects,
}) => {
  const [swalProps, setSwalProps] = React.useState({});

  const handleClick = () => {
    setSwalProps({
      show: true,
      showCloseButton: true,
      confirmButtonColor: "#f17d30",
    });
  };

  return (
    <Card className="my-2 d-flex flex-column flex-md-row justify-content-center align-items-center">
      <span className="col-12 col-md-4 align-self-center">
        <Clientes
          reporteCliente={reporteCliente}
          clientSelected={clientSelected}
          clientSelectedId={clientSelectedId}
          setClientSelected={setClientSelected}
          setClientSelectedId={setClientSelectedId}
          setProjectSelected={setProjectSelected}
        />
      </span>
      <span className="col-12 col-md-4 align-self-center">
        <Proyecto
          projectSelected={projectSelected}
          projects={projects}
          setProjectSelected={setProjectSelected}
        />
      </span>
      <span className="col-4 align-self-center my-2">
        <button className="btn btn-outline-dark" onClick={handleClick}>
          Fechas
        </button>
        <SweetAlert2
          {...swalProps}
          width={980}
          didClose={() =>
            setSwalProps({
              show: false,
            })
          }
        >
          <Calendar datePicker={datePicker} onChange={onChange} />
        </SweetAlert2>
      </span>
      {/* <span className="col-3">
        <Usuarios />
      </span> */}

      {/* <Tipo /> */}
    </Card>
  );
};

export default DropDowns;
